/* eslint-disable */

const factoryFrete = (element, id) => {
  const name = getName(element)
  const price = getPrice(element)
  const description = getDescription(element)
  const checked = getChecked(element)

  return { name, price, description, checked, id }
}

const getName = (element) => {
  return element.find('label').text().split(':')[0].trim()
}

const getPrice = (element) => {
  const value = (element.find('label').text().split(':')[1] || '').trim()
  return parseTextMoneyToFloat(value)
}

const getDescription = (element) => {
  return element.find('p').text().trim()
}

const getChecked = (element) => {
  return Boolean(element.find('input').attr('checked'))
}

const parseTextMoneyToFloat = (textValue) => {
  if (textValue === '') return 0
  return Number(
    textValue
      .replace('R$', '')
      .replace(/\,/g, '.')
  )
}

const loadFretes = () => {
  const elementUl = jQuery('#shipping_method')
  const elementsList = elementUl.find('li')

  const fretes = Array.from(elementsList).map((el, index) => factoryFrete(jQuery(el), index))
  return fretes
}

export default loadFretes
