/* eslint-disable */

const parseTextMoneyToFloat = (textValue) => {
  return Number(
    textValue
      .replace('R$', '')
      .replace(/\,/g, '.')
  )
}

const getTextByElement = (element) => {
  return element.text().trim()
}

const getFloatByElement = (element) => {
  return parseTextMoneyToFloat(getTextByElement(element))
}

const loadSubTotal = () => {
  const element = jQuery('tr.cart-subtotal td[data-title="Subtotal"]')
  return getFloatByElement(element)
}

export default loadSubTotal
