import awaitLoadingBlockUI from './awaitLoading'

const getElementInputById = () => {
  const input = jQuery('#shipping_method').eq(0)
  return input
}

const setInput = async (input, id) => {
  const inputChecked = input.find('input').eq(id)
  inputChecked.click()
  return true
}

const updateFrete = async (id) => {
  const input = getElementInputById()
  await setInput(input, id)
}

export default async (id) => awaitLoadingBlockUI(() => updateFrete(id))
