/* eslint-disable */

import awaitLoadingBlockUI from './awaitLoading'

const getElementInputById = () => {
  const input = jQuery('#calc_shipping_postcode').eq(0)
  return input
}

const setInput = async (input, value) => {
  input.val(value)
  input.change()
  return true
}

const updateCart = async () => {
  jQuery('button[name="calc_shipping"]').click()
  return true
}

const updateCep = async (value) => {
  const inputCep = getElementInputById()
  await setInput(inputCep, value)
  await updateCart()
}

export default async (cep) => awaitLoadingBlockUI(() => updateCep(cep))
