/* eslint-disable */

import awaitLoadingBlockUI from './awaitLoading'

const remove = async (id) => {
  const button = getElementButtonById(id)
  return button.click()
}

const getElementButtonById = (id) => {
  const button = jQuery(`a[data-product_id="${id}"]`)
  return button
}

export default async (id) => awaitLoadingBlockUI(() => remove(id))
