<template>
  <div>
    <b-form-select :value="value" :options="options" size="sm" class="qtd-field" @change="update"/>
    <div class="remove mt-1">
      <a href="#" class="text-remove-item">
        <span class="cart-remove-item" @click="remove">X Remover</span>
      </a>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ProductQtd',
  props: ['qtd', 'product'],
  data () {
    return {
      options: Array(10).fill(null).map((_, i) => i).slice(1, 10),
      value: this.qtd
    }
  },
  watch: {
    qtd (qtd) {
      this.value = qtd
    }
  },
  methods: {
    update (value) {
      this.$emit('update', { id: this.product.id, value })
    },
    remove () {
      this.$emit('remove', this.product.id)
    }
  }
}
</script>

