/* eslint-disable */

const blockUI = () => {
  const html = `<div class="loading-overlay" style="
    position: fixed;
    width: 100%;
    height: 100%;
    background: #0000004a;
    z-index: 2;
    display: inline-block;
    top: 0;
    left: 0;
">
    <svg version="1.1" id="L4" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" viewBox="0 0 100 100" enable-background="new 0 0 0 0" xml:space="preserve" style="
    width: 150px;
    top: 50%;
    position: absolute;
    margin-top: -75px;
    margin-left: -45px;
    left: 50%;
">
        <circle fill="#fff" stroke="none" cx="6" cy="50" r="6">
          <animate attributeName="opacity" dur="1s" values="0;1;0" repeatCount="indefinite" begin="0.1"></animate>
        </circle>
      <circle fill="#fff" stroke="none" cx="26" cy="50" r="6">
          <animate attributeName="opacity" dur="1s" values="0;1;0" repeatCount="indefinite" begin="0.2"></animate>
        </circle>
      <circle fill="#fff" stroke="none" cx="46" cy="50" r="6">
          <animate attributeName="opacity" dur="1s" values="0;1;0" repeatCount="indefinite" begin="0.3"></animate>
        </circle>
      </svg>
  </div>`

  const div = document.createElement('div')
  div.innerHTML = html

  document.querySelector('body').appendChild(div.querySelector('.loading-overlay'))
}

const unblockUI = () => {
  if (document.querySelector('.loading-overlay')) {
    document.querySelector('.loading-overlay').remove()
  }
}

export default { blockUI, unblockUI }
