/* eslint-disable */

const factory = async (action) => {
  const getState = (state) => {
    if (state === 0 && jQuery('.blockUI').length > 0) {
      return 1
    }
    if (state === 1 && jQuery('.blockUI').length === 0) {
      return 2
    }
    return state
  }

  let interval
  let timeout

  const complete = (resolve) => {
    clearInterval(interval)
    clearTimeout(timeout)
    resolve()
  }

  const awaitLoad = () => new Promise(resolve => {
    action()
    let state = 0
    interval = setInterval(() => {
      state = getState(state)
      if (state === 2) {
        complete(resolve)
      }
    }, 100)

    timeout = setTimeout(() => complete(resolve), 6000)
  })

  return awaitLoad()
}

export default factory
