/* eslint-disable */

import awaitLoadingBlockUI from './awaitLoading'

const updateQtd = async (id, value) => {
  const inputQtd = getElementInputById(id)
  await setInput(inputQtd, value)
  await updateCart()
}

const getElementInputById = (id) => {
  const element = jQuery('[data-product_id="' + id + '"]').parents('tr').find('td.product-quantity')
  const input = element.find('input').eq(0)
  return input
}

const setInput = async (input, value) => {
  input.val(value)
  input.change()
  return true
}

const updateCart = async () => {
  jQuery('button[name="update_cart"]').click()
  return true
}

export default async (id, value) => awaitLoadingBlockUI(() => updateQtd(id, value))
