/* eslint-disable */

const factoryProduct = (elementTr) => {
  const name = getTextByElement(elementTr.find('td.product-name'))
  const price = getFloatByElement(elementTr.find('td.product-price'))
  const qtd = getValueFloatByElement(elementTr.find('td.product-quantity'))
  const imgUrl = getImageByElement(elementTr.find('td.product-thumbnail'))
  const id = Number(elementTr.find('[data-product_id]').attr('data-product_id'))

  return { name, price, qtd, imgUrl, id }
}

const getTextByElement = (element) => {
  return element.text().trim()
}

const getValueByElement = (element) => {
  return element.find('input').val().trim()
}

const getImageByElement = (element) => {
  return element.find('img').attr('src')
}

const getValueFloatByElement = (element) => {
  return Number(getValueByElement(element))
}

const parseTextMoneyToFloat = (textValue) => {
  return Number(
    textValue
      .replace('R$', '')
      .replace(/\,/g, '.')
  )
}

const getFloatByElement = (element) => {
  return parseTextMoneyToFloat(getTextByElement(element))
}

const loadProducts = () => {
  jQuery('#content > h1').hide()

  const elementTable = jQuery('table.cart')
  const elementsTrList = elementTable.find('tr.cart_item')

  const products = Array.from(elementsTrList).map((el) => factoryProduct(jQuery(el)))
  return products
}

export default loadProducts
