<template>
  <div>
    <hr>
    <h3>Prazo de Entrega</h3>
    <label class="product-delivery-time-description">
      Informe o CEP para consulta
      <a target="_blank" class="product-delivery-time-external-link" href="http://www.buscacep.correios.com.br/servicos/dnec/index.do">
        (Não sei meu CEP)
      </a>
    </label>

    <b-row class="mb-4">
      <b-col class="px-0 pl-3">
        <b-form-input class="input-cep" name="cep" type="text" size="md" placeholder="00000-000" v-model="cepValue" />
      </b-col>
      <b-col cols="auto" class="px-0 pr-3">
        <b-button variant="outline-info" class="ml-2 btn-cep" @click="update">OK</b-button>
      </b-col>
    </b-row>

    <hr class="mb-0">
  </div>
</template>

<script>
export default {
  name: 'CartCep',
  props: ['cep'],
  data () {
    return {
      cepValue: this.cep || ''
    }
  },
  methods: {
    update () {
      const numbers = this.cepValue.replace(/\D/g, '')
      this.$emit('update', numbers)
    }
  }
}
</script>

