<template>
  <div class="CartTotal">
    <h3 class="cart-total-title mb-4">Resumo</h3>
    <b-row>
      <b-col xs="8" cols="8" class="p-0 pl-3 mw-50">
        <h4>Subtotal</h4>
      </b-col>
      <b-col xs="4" cols="4" class="p-0 mw-50">
        <span>R$ {{ money }}</span>
      </b-col>
    </b-row>
    <b-row v-show="totalFrete > 0">
      <b-col xs="8" cols="8" class="p-0 pl-3 mw-50">
        <h4>Frete</h4>
      </b-col>
      <b-col xs="4" cols="4" class="p-0 mw-50">
        <span>R$ {{ formatMoney(totalFrete) }}</span>
      </b-col>
    </b-row>
    <b-row v-show="discount > 0">
      <b-col xs="8" cols="8" class="p-0 pl-3 mw-50">
        <h4>Desconto</h4>
      </b-col>
      <b-col xs="4" cols="4" class="p-0 mw-50">
        <span>- R$ {{ formatMoney(discount) }}</span>
      </b-col>
    </b-row>
    <b-row>
      <b-col xs="8" cols="8" class="p-0 pl-3 mw-50">
        <h4>Total</h4>
      </b-col>
      <b-col xs="4" cols="4" class="p-0 mw-50">
        <span class="total">R$ {{ formatMoney(total) }}</span>
      </b-col>
    </b-row>
  </div>
</template>

<script>
import formatMoney from '../libs/formatMoney'

export default {
  name: 'CartTotal',
  props: ['subtotal', 'discount', 'total', 'fretes'],
  computed: {
    money () {
      return formatMoney(this.subtotal)
    },
    totalFrete () {
      const frete = this.fretes.find(f => f.checked)
      if (!frete) {
        return 0
      }
      return frete.price
    }
  },
  methods: {
    formatMoney (value) {
      return formatMoney(value)
    }
  }
}
</script>

