/* eslint-disable */

import awaitLoadingBlockUI from './awaitLoading'

const removeCupon = async () => {
  const button = getElementButtonById()
  return button.click()
}

const getElementButtonById = () => {
  const button = jQuery(`.woocommerce-remove-coupon`)
  return button
}

export default async () => awaitLoadingBlockUI(() => removeCupon())
