/* eslint-disable */

import awaitLoadingBlockUI from './awaitLoading'

const getElementInputById = () => {
  const input = jQuery('#coupon_code')
  return input
}

const setInput = async (input, value) => {
  input.val(value)
  input.change()
  return true
}

const updateCart = async () => {
  jQuery('button[name="apply_coupon"]').click()
  return true
}

const updateCupon = async (code) => {
  const inputCupom = getElementInputById()
  await setInput(inputCupom, code)
  await updateCart()
}

export default async (code) => awaitLoadingBlockUI(() => updateCupon(code))
