<template>
  <div class="CartTitle">
    <hr>
    <h3>Sacola de Compras - CorpusFit</h3>
    <span>{{ itensQtd }} {{ itemTitle }}</span>
  </div>
</template>

<script>
export default {
  props: ['products'],
  name: 'CartTitle',
  computed: {
    itensQtd () {
      if (!this.products) {
        return 0
      }
      return this.products.length
    },
    itemTitle () {
      if (this.itensQtd > 1) {
        return 'items'
      }
      return 'item'
    }
  }
}
</script>

