<template>
  <div class="ProductComplete text-center">
    <div class="column-price d-inline-block">
      <span class="installments">
        12x de <span class="price">R$ {{ installmentValue }}</span> sem juros, ou R$ {{ money }}
      </span>
    </div>
    <BtnComplete />
    <BtnContinueBuy />
  </div>
</template>

<script>
import BtnComplete from './BtnComplete'
import BtnContinueBuy from './BtnContinueBuy'
import formatMoney from '../libs/formatMoney'

export default {
  name: 'ProductComplete',
  props: ['total'],
  components: { BtnComplete, BtnContinueBuy },
  computed: {
    installmentValue () {
      const value = this.total
      return formatMoney(Math.round((value / 12) * 100) / 100)
    },
    money () {
      return formatMoney(this.total)
    }
  }
}
</script>

