<template>
  <div>
    <b-form-group>
      <b-row v-for="frete in fretes" :key="frete.id" class="frete-row" :class="{ 'checked': frete.checked }">
        <b-col cols="auto">
          <b-form-radio v-model="selected" name="frete-radios" :value="frete.id" class="frete-radios" @change="() => update(frete.id)" />
        </b-col>
        <b-col class="frete-col">
          <h3>{{ frete.name }}</h3>
          <span>{{ frete.description }}</span>
        </b-col>
        <b-col cols="auto" v-if="frete.price > 0">
          <h2>R$ {{ formatMoney(frete.price) }}</h2>
        </b-col>
        <b-col cols="auto free" v-else>
          <h2>Grátis</h2>
        </b-col>
      </b-row>
    </b-form-group>
  </div>
</template>

<script>
import formatMoney from '../libs/formatMoney'

export default {
  name: 'CartFrete',
  props: ['fretes'],
  data () {
    return {
      selected: ''
    }
  },
  mounted () {
    setTimeout(() => {
      const frete = this.fretes.find(frete => frete.checked)
      if (frete) {
        this.selected = frete.id
      }
    }, 500)
  },
  methods: {
    formatMoney (value) {
      return formatMoney(value)
    },
    update (index) {
      return this.$emit('update', index)
    }
  }
}
</script>

