<template>
  <div class="carinho">

    <b-row v-if="!hasProducts" class="mt-3">
      <b-col>
        <EmpityCart />
      </b-col>
    </b-row>

    <b-row v-if="hasProducts" class="mt-3">
      <b-col>
        <BtnComplete />
      </b-col>
    </b-row>
    <b-row v-if="hasProducts">
      <b-col>
        <CartTitle :products="products" />
      </b-col>
    </b-row>

    <b-row v-show="hasProducts" class="mt-4" v-for="(product, index) in products" :key="index">
      <b-col cols="4" xs="4">
        <ProductImage :img="product.imgUrl" />
      </b-col>
      <b-col cols="8" xs="8" class="mw-66 px-0">
        <ProductTitle :title="product.name" />
        <ProductQtd :qtd="product.qtd" :product="product" class="mt-3" @update="updateQtd" @remove="removeProduct" />
      </b-col>
    </b-row>

    <b-row v-if="hasProducts">
      <b-col>
        <CartCep :cep="cep" @update="updateCep" />
      </b-col>
    </b-row>

    <b-row v-if="hasProducts">
      <b-col>
        <CartFrete :fretes="fretes" @update="updateFrete" />
      </b-col>
    </b-row>

    <b-row v-if="hasProducts">
      <b-col>
        <CartCupom :cuponCode="cupon" @update="updateCupon" @remove="removeCupon" />
      </b-col>
    </b-row>

    <b-row v-if="hasProducts">
      <b-col>
        <CartTotal :subtotal="subTotal" :discount="cuponDiscount" :total="total" :fretes="fretes" />
      </b-col>
    </b-row>

    <b-row v-if="hasProducts" style="margin-bottom: 4rem;">
      <b-col>
        <ProductComplete :total="total" />
      </b-col>
    </b-row>
  </div>
</template>

<script>
import BtnComplete from './BtnComplete'
import CartTitle from './CartTitle'
import ProductImage from './ProductImage'
import ProductTitle from './ProductTitle'
import ProductQtd from './ProductQtd'
import CartCep from './CartCep'
import CartTotal from './CartTotal'
import ProductComplete from './ProductComplete'
import CartFrete from './CartFrete'
import CartCupom from './CartCupom'
import EmpityCart from './EmpityCart'
import api from '../libs/api'
import block from '../libs/block'

export default {
  name: 'Carrinho',
  components: { CartFrete, ProductTitle, CartTitle, BtnComplete, ProductImage, ProductQtd, CartCep, CartTotal, ProductComplete, CartCupom, EmpityCart },
  data () {
    return {
      products: [],
      fretes: [],
      subTotal: 0,
      total: 0,
      cuponDiscount: 0,
      cep: '',
      cupon: ''
    }
  },
  mounted () {
    this.loadAll()
  },
  computed: {
    hasProducts () {
      return this.products.length > 0
    }
  },
  methods: {
    loadAll () {
      this.products = api.loadProducts()
      this.fretes = api.loadFretes()
      this.subTotal = api.loadSubTotal()
      this.total = api.loadTotal()
      this.cep = api.loadCep()
      this.cupon = api.loadCupon()
      this.cuponDiscount = api.loadCuponDiscount()
      block.unblockUI()
    },
    updateQtd ({ id, value }) {
      block.blockUI()
      api.updateQtd(id, value).then(() => {
        this.loadAll()
      })
    },
    updateCep (cep) {
      block.blockUI()
      api.updateCep(cep).then(() => {
        this.loadAll()
      })
    },
    updateFrete (index) {
      block.blockUI()
      api.updateFrete(index).then(() => {
        this.loadAll()
      })
    },
    removeProduct (id) {
      block.blockUI()
      api.remove(id).then(() => {
        this.loadAll()
      })
    },
    updateCupon (code) {
      block.blockUI()
      api.updateCupon(code).then(() => {
        api.updateCupon(code).then(() => {
          this.loadAll()
        })
      })
    },
    removeCupon () {
      block.blockUI()
      api.removeCupon().then(() => {
        window.location.reload()
      })
    }
  }
}
</script>

