<template>
  <div class="box-alert-coupons row">
    <h3 class="subtitle">Cupons e Vales</h3>
    Possui um Cupom de desconto ou Vale-troca?
    <a class="btn-show" @click="showCupon">Utilize-o aqui e aproveite!</a>
    <b-row class="mt-3 form" v-show="show || hasCupon">
      <b-col class="px-0">
        <b-form-input class="input-cupon" name="input-cupon-corpus" type="text" size="md" placeholder="Digite seu Cupom" v-model="code" :readonly="hasCupon" />
      </b-col>
      <b-col cols="auto" class="px-0">
        <b-button v-if="!hasCupon" variant="outline-info" class="ml-2 btn-cupon-info" @click="update">Aplicar</b-button>
        <b-button v-else variant="outline-danger" class="ml-2 btn-cupon-danger" @click="remove">Remover</b-button>
      </b-col>
    </b-row>
  </div>
</template>

<script>
export default {
  name: 'CartCupom',
  props: ['cuponCode'],
  data () {
    return {
      code: this.cuponCode || '',
      show: false
    }
  },
  watch: {
    cuponCode () {
      this.code = this.cuponCode || ''
    }
  },
  methods: {
    update () {
      console.log('update: ', this.code)
      this.$emit('update', this.code)
    },
    remove () {
      this.$emit('remove')
    },
    showCupon () {
      this.show = true
    }
  },
  computed: {
    hasCupon () {
      return Boolean(this.cuponCode && this.cuponCode !== '')
    }
  }
}
</script>

