import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import { BootstrapVue } from 'bootstrap-vue'

import Vue from 'vue'
import App from './App.vue'
import getIsCartPage from './libs/getIsCartPage'

Vue.use(BootstrapVue)
Vue.config.productionTip = false

const init = () => {
  const isCartPage = getIsCartPage()
  if (isCartPage === false) {
    return false
  }

  let app
  app = '#app'

  if (document.querySelector('article')) {
    document.querySelector('article').style.display = 'none'
  }

  if (document.querySelector('#content')) {
    app = document.createElement('div')
    document.querySelector('#content').prepend(app)
    document.querySelector('#content').classList.add('px-0')
    document.querySelector('.carrinho-conteudo').classList.add('px-0')
    app.classList.add('px-0')
  }

  if (app) {
    new Vue({
      render: h => h(App)
    }).$mount(app)
  }

  return true
}

init()
